<template>
  <scroll-view class="max_width">
    <div>
      <main-title :thisPage=thisPage @toSearch='toSearch' :hideSearch=hideSearch></main-title>
      <div class="contentBox">
        <div class="listBox" v-if="listShow" :class="{listHide:listHide}">
          <div :key=key v-for="(x,key) in dataList">
            <index-card :animation='animation' :goods=x @toDetail='toDetail' :index='key' :leftNone='leftNone'></index-card>
          </div>
          <station v-if="dataList.length==0 && loading"></station>
        </div>
        <div class="searchBox" v-if="listHide">
          <search-box :searchNew=searchNew :searchPage=thisPage></search-box>
        </div>
      </div>
    </div>

  </scroll-view>
</template>


<script>
import mainTitle from "@/components/mainTitle";
import indexCard from "@/components/indexCard";
import searchBox from "@/components/searchBox";
import station from "@/components/station";

export default {
  data() {
    return {
      loading: false,
      thisPage: "childIndex",
      dataPre: {},
      leftNone: false,
      prePage: undefined,
      listShow: true,
      listHide: false,
      dataList: [],
      hideSearch: false,
      searchNew: 0,
      animation: true,
      title: ""
    };
  },

  components: {
    mainTitle,
    indexCard,
    searchBox,
    station
  },

  methods: {
    escape2Html(str) {
      str = str || "";

      var arrEntities = {
        lt: "<",
        gt: ">",
        nbsp: " ",
        amp: "&",
        quot: '"'
      };
      return str.replace(/&(lt|gt|nbsp|amp|quot);/gi, function(all, t) {
        return arrEntities[t];
      });
    },
    toSearch() {
      this.hideSearch = true;
      this.listHide = true;
      setTimeout(() => {
        this.listShow = false;
        this.searchNew = +new Date();
      }, 500);
    },
    getList() {
      this.leftNone = false;
      setTimeout(() => {
        this.leftNone = true;
      }, 200);
    },
    toDetail(x) {
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .get(`${wx.getStorageSync("url")}/type/${x.id}`, {})
        .then(d => {
          //输出请求数据
          console.log("req", d.data);
          wx.setStorage({
            key: "goods",
            data: JSON.stringify(x)
          });
          wx.setStorageSync("pre_page", this.thisPage);
          var url;
          if (d.data.data.type == "series") {
            wx.setStorageSync("childIndex2", {
              title: x.title,
              data: d.data
            });
            url = "../childIndex2/main";
            wx.setStorageSync("share_childIndex2", x);
          } else {
            wx.setStorageSync("explain", {
              title: x.title,
              data: d.data
            });

            url = "../explain/main";
            wx.setStorageSync("share_explain", x);
            wx.setStorageSync("share_player_list", x.id);
          }
          wx.navigateTo({
            url
          });
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
    },
    showDetail(option) {
      var x = {
        id: option.key1,
        title: option.tilte
      };
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .get(`${wx.getStorageSync("url")}/type/${x.id}`, {})
        .then(d => {
          //输出请求数据
          console.log("req", d.data);
          wx.setStorage({
            key: "goods",
            data: JSON.stringify(x)
          });
          wx.setStorageSync("pre_page", this.thisPage);

          var url;
          if (d.data.data.type == "series") {
            wx.setStorageSync("childIndex", {
              title: x.title,
              data: d.data
            });
          } else {
            wx.setStorageSync("explain", {
              title: x.title,
              data: d.data
            });
          }
          this.showStart();
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
    },
    exit() {
      // var SP = wx.getStorageSync("search_page");
      // if (SP) {
      //   wx.setStorageSync("search_page", false);
      //   return;
      // }

      this.leftNone = false;
      this.listShow = true;
      this.listHide = false;
      this.dataList = [];
      this.hideSearch = false;
      this.animation = true;
      this._watchers = [];
    },
    showStart() {
      wx.showShareMenu({
        withShareTicket: false,
        success: () => {},
        fail: () => {},
        complete: () => {}
      });

      this.prePage = wx.getStorageSync("pre_page");
      if (this.prePage == "none") {
        this.animation = false;
      } else {
        this.exit();
      }

      var data = wx.getStorageSync("childIndex");
      console.log(data);
      this.dataList = data.data.data.list;
      this.title = this.escape2Html(data.title) || "大疆飞手百科";
      wx.setStorageSync("pre_page", "none");
      wx.setNavigationBarTitle({
        title: this.escape2Html(this.title) //页面标题为路由参数
      });
      this.getList();
    },
    getQueryString: function(url, name) {
      var reg = new RegExp("(^|&|/?)" + name + "=([^&|/?]*)(&|/?|$)", "i");
      var r = url.substr(1).match(reg);
      if (r != null) {
        return r[2];
      }
      return null;
    }
  },
  created() {},
  onLoad(options) {
    console.log("options", options);
    // 线上
    wx.setStorageSync("url", "https://feishou-baike.djiits.com");
    wx.setStorageSync("media_url", "https://feishou-baike.djicdn.com");
    if (options.q) {
      let q = decodeURIComponent(options.q);
      wx.setStorageSync("options", {
        share: true,
        key1: this.getQueryString(q, "key1"),
        title: "大疆飞手百科"
      });
    } else {
      wx.setStorageSync("options", options);
    }
  },
  onShow() {
    this.loading = false;
    var options = wx.getStorageSync("options");
    console.log("k", options);
    wx.setStorageSync("options", {});
    if (options.share) {
      console.log(`/pages/childIndex/main?share=true&key1=${options.key1}`);
      wx.setStorageSync("o_key1", options.key1);
      this.showDetail(options);
    } else {
      this.loading = true;

      this.showStart();
    }
  },
  onHide() {
    this.exit();
  },
  onUnload() {
    this.exit();
  },
  onShareAppMessage: function() {
    var x = wx.getStorageSync("share_childIndex") || {};
    if (!x.id) {
      x.id = wx.getStorageSync("o_key1");
    }
    var path = `/pages/childIndex/main?share=true&key1=${x.id}&title=${
      this.title
    }`;
    return {
      title: this.title,
      path: path,
      success(res) {
        console.log("转发成功!", res, path);
      },
      fail(res) {
        console.log("转发失败!", res);
      }
    };
  }
};
</script>

<style scoped lang='scss'>
.listBox {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  transition-timing-function: ease-in;
  -webkit-transform: translate3d(0, 0, 0);
  /*开启硬件加速*/
  transform: translateY(0);
  transition: transform 500ms;
  // position: absolute;
  top: 0;
  z-index: 200;
  width: 100%;
}

.searchBox {
  // position: absolute;
  top: 0;
  z-index: 100;
  width: 100%;
}

.listHide {
  transform: translateY(100%);
}

.contentBox {
  position: relative;
}
</style>



